import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { CommonVariables } from 'src/app/shared/commmon';
import { CommonResponse } from 'src/app/shared/commmon/ServiceClass';
import { map } from 'rxjs/operators';

@Injectable({
  providedIn: 'root'
})
export class CustomerDishIngredientsService {

  constructor(private util: CommonVariables, private http: HttpClient) { }
  // get dish details by dish id
  GetDishDetailsByDishId(DishId) {
    const url = this.util.GetCustomerDishIngredients + DishId;
    return this.http.get<CommonResponse>(url).pipe(map((response: CommonResponse) => response));
  }
}
