import { Component, OnInit } from '@angular/core';
import { CustomerDishIngredientsService } from './customer-dish-ingredients.service';
import { ActivatedRoute } from '@angular/router';
import { CommonVariables } from 'src/app/shared/commmon';
import { ViewMediaComponent } from '../view-media/view-media.component';
import { MatDialog } from '@angular/material';

@Component({
  selector: 'app-dish-ingredients',
  templateUrl: './dish-ingredients.component.html',
  styleUrls: ['./dish-ingredients.component.scss']
})
export class DishIngredientsComponent implements OnInit {


  DishID = 0;
  DishDetails: DishDetails = {
    Title: '',
    ChefName: '',
    Description: '',
    Images: '',
    Ingredients: '',
    NutritionValues: '',
    Videos: '',
    ProfilePic: ''
  };
  constructor(private service: CustomerDishIngredientsService, private route: ActivatedRoute,
    private util: CommonVariables, private dialog: MatDialog) {
    this.GetDishDetails();
  }

  ngOnInit() {
  }
  // get dish details by dish id
  GetDishDetails() {
    this.DishID = parseInt(this.route.snapshot.paramMap.get('id'));
    if (!(isNaN(this.DishID)) && this.DishID !== 0) {
    this.service.GetDishDetailsByDishId(this.DishID).subscribe(Response => {
      if (Response.Status === true) {
        this.DishDetails = Response.Data;
      }
    });
  }
  }
  // get chef profile pic
  GetImageLocation(filename) {
    let FinalLocation = '';
    if (filename == null) {
      FinalLocation = 'url(/assets/images/chef-placeholder.jpg)';
    } else {
      FinalLocation = 'url(' + this.util.base_url + filename + ')';
    }
    return FinalLocation;
  }
   // show video
   OpenMedia(FileLocation, SourceType) {
    const FinalUrl = this.util.base_url + FileLocation;
    const dialogRef = this.dialog.open(ViewMediaComponent, {
    panelClass: 'MediaDialogBox',
    disableClose: true,
    data: {'MediaSource': FinalUrl, 'MediaType': SourceType}
    });
  // fires when dialogue closed
    dialogRef.afterClosed().subscribe(result => {
    //
    });
}
// get cover image
GetCoverImage(){
  let FinalUrl='';
  if(this.DishDetails.Images.length>0){
    FinalUrl=this.util.base_url+this.DishDetails.Images[0].FileLocation;
  }else{
    FinalUrl="assets/images/chocolate-tea.jpg";
  }
 return FinalUrl;
}
}


export interface DishDetails {
  Title;
Ingredients;
NutritionValues;
Description;
Videos;
Images;
ChefName;
ProfilePic;
}
